*{
    margin: 0;
    padding: 0;
}

.inputForm{
    background-color: bisque;
    margin-top: 50px;
    padding: 50px;
    border-radius: 5px;
    width: 360px;
    height: 400px;
    margin-left: 30px;
}
.inputForm input{
    width: 300px;
    padding: 10px 20px;
    outline: none;
    margin-bottom: 20px;
    font-size: 15px;
}
.inputForm textarea{
    width: 300px;
    padding: 10px 20px;
    outline: none;
    margin-bottom: 20px;
    font-size: 15px;
    margin-left: 5px;
}
.inputForm button{
    background-color: rgb(107, 107, 226);
    color: white;
    padding: 5px 20px;
    font-size: 15px;
    border: none;
    margin-left: 130px;
    cursor: pointer;
    
   
}